  .bdbg-header {
    margin-bottom: 0.5rem;
  }
}

.admin-bar .bdbg-header.navbar-fixed {
  top: 46px;
}

@media screen and (min-width: 783px) {
  .admin-bar .bdbg-header.navbar-fixed {
    top: 32px;
  }
}

.admin-bar .bdbg-menu--side {
  top: 46px;
}

@media screen and (min-width: 783px) {
  .admin-bar .bdbg-menu--side {
    top: 32px;
  }
}

html {
  margin-top: 0 !important;
}

.bdbg-logo {
  height: 100%;
  max-height: 100%;
}

html #wpadminbar {
  position: fixed;
}

.row {
  margin-bottom: 0;
}

.container {
  width: 90%;
  max-width: 1280px;
}

.card {
  display: inline-block;
  width: 100%;
}

@media screen and (max-width: 600px) {
  .card {
    margin: 1rem 0;
  }
}

.card .card-action a:not(.btn):not(.btn-large):not(.btn-large):not(.btn-floating) {
  text-transform: none;
}

.bdbg-comments .bdbg-comments__avatar {
  word-break: break-word;
  text-align: center;
}

.bdbg-comments .bdbg-comments__avatar img {
  border-radius: 50%;
}

.bdbg-comments .children {
  margin-left: 2em;
}

.bdbg-comments .bdbg-comments__item {
  border-bottom: none;
}

@media screen and (max-width: 600px) {
  .bdbg-comments .bdbg-comments__item {
    flex-direction: column;
    margin-bottom: 2em;
  }
}

.bdbg-comments__item {
  margin-bottom: 1rem;
}

.bdbg-comments__list {
  border: none;
}

.bdbg-commetns__reply {
  margin-top: 1em;
}

.bdbg-comments__body {
  padding: 1rem 1.5rem;
  color: #fff;
  background-color: rgba(255, 64, 129, 0.75);
  position: relative;
}

.bdbg-comments__body:before {
  width: 1rem;
  height: 1rem;
  border-top: 1rem solid transparent;
  border-bottom: 1rem solid transparent;
  border-right: 1rem solid rgba(255, 64, 129, 0.75);
  position: absolute;
  left: -1rem;
  top: 50%;
  content: "";
  display: block;
  margin-top: -1rem;
}

.bdbg-commentform .bdbg-commentform__submit {
  margin: 0.75rem;
}

.paginate-com {
  text-align: center;
  margin-bottom: 1.5rem;
}

.paginate-com .page-numbers {
  color: white;
  display: inline-block;
  font-size: 1.2rem;
  padding: 0 10px;
  line-height: 30px;
  border-radius: 2px;
}

.paginate-com .page-numbers.current {
  background-color: transparent;
  color: #999;
  font-weight: bold;
}

.paginate-com .page-numbers i {
  vertical-align: middle;
}

#cancel-comment-reply-link {
  display: block;
}

.comment-respond h5 {
  text-transform: uppercase;
}

.logged-in-as,
.comment-notes {
  padding: 0 1rem;
}

.bdbg-logo--main {
  vertical-align: middle;
}

.bdbg-logo--main.left {
  left: 0;
}

.bdbg-logo--main.right {
  right: 0;
}

.bdbg-logo--main .bdbg-logo__img {
  vertical-align: middle;
}

.bdbg-message a {
  color: #FFC0CB;
  line-height: 60px;
}

.bdbg-overlay {
  position: fixed;
  z-index: 9999;
  width: 100%;
  height: 100%;
  background-color: rgba(238, 110, 115, 0.6);
  color: #fff;
  padding: 3em;
  transition: transform 1s;
  bottom: 0;
  left: 0;
  transform: translateY(-100%);
}

.bdbg-overlay.bdbg-overlay--visible {
  transform: translateY(0);
}

.bdbg-overlay #searchform .bdbg-input-group__label {
  color: #fff;
}

.bdbg-overlay #searchform .bdbg-input-group__field {
  color: #fff;
  border-color: rgba(255, 255, 255, 0.5);
  box-shadow: none;
  font-size: 2em;
  text-align: center;
  height: auto;
}

.bdbg-overlay #searchform .bdbg-input-group__field:focus {
  color: #fff;
  border-bottom: 1px solid #fff;
  box-shadow: none;
}

.bdbg-overlay #searchform .bdbg-input-group__field:focus + label {
  color: #fff;
}

.bdbg-modal {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  width: 80%;
  max-width: 50rem;
}

.bdbg-overlay__button {
  text-align: right;
  font-size: 2.5rem;
  cursor: pointer;
}

.bdbg-overlay__button .fa {
  transition: transform 0.5s;
}

.bdbg-overlay__button .fa:hover {
  transform: scale(1.5);
  transform-origin: 50% 50%;
}

.bdbg-nav {
  height: 64px;
  z-index: 20;
  position: relative;
}

.bdbg-menu--main {
  height: auto;
}

.bdbg-menu--main .dropdown-button::after {
  display: inline-block;
  font: normal normal normal 14px/1 FontAwesome;
  font-size: inherit;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  content: "\f0d7";
  float: right;
  line-height: inherit;
  transition: 0.5s;
  margin-left: 1em;
}

.bdbg-menu--main .dropdown-button.active::after {
  transform: rotate(180deg);
}

.bdbg-menu--side {
  padding-top: 2em;
  padding-bottom: 2em;
  color: #000;
  display: block;
  width: 300px;
}

.bdbg-menu--side a {
  padding: 0 32px;
  color: #3d3d3d;
}

.bdbg-menu--side a:hover {
  color: #3d3d3d;
}

.bdbg-menu--side .bdbg-message {
  padding: 1rem 32px;
  display: block;
  line-height: 1;
}

.bdbg-menu--side .bdbg-message a {
  display: inline;
  padding: 0;
}

.bdbg-menu--side .collapsible-header {
  padding: 0 32px;
}

.bdbg-menu--side .bdbg-logo--side {
  display: block;
  height: auto;
}

.bdbg-menu--side li {
  line-height: inherit;
}

.bdbg-menu--side .collapsible-header {
  line-height: 48px;
  height: auto;
}

.bdbg-menu--side .collapsible-header::after {
  display: inline-block;
  font: normal normal normal 14px/1 FontAwesome;
  font-size: inherit;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  content: "\f0d7";
  float: right;
  line-height: inherit;
  transition: 0.5s;
}

.bdbg-menu--side .active .collapsible-header::after {
  transform: rotate(180deg);
}

.bdbg-logo__img {
  height: auto;
  width: auto;
}

.dropdown-content {
  width: 180px;
}

.bdbg-header .button-collapse i {
  font-size: 1.95rem;
  vertical-align: middle;
}

@media screen and (max-width: 1024px) {
  .bdbg-header .bdbg-nav .bdbg-logo--main {
    margin-left: auto;
    margin-right: auto;
    left: auto;
    right: auto;
    left: 50%;
    transform: translateX(-50%);
  }
}

.bdbg-pagination {
  display: inline-block;
  margin-bottom: 2rem;
}

.bdbg-pagination li.active {
  background-color: #FF4081;
}

.bdbg-pagination li i {
  font-size: 1em;
}

.bdbg-post__featured {
  position: absolute;
  top: 1rem;
  left: 1rem;
  line-height: 1;
}

.bdbg-post__featured i {
  font-size: 3rem;
}

.card .card-action,
.card .card-action a:not(.btn):not(.btn-large):not(.btn-large):not(.btn-floating) {
  color: #9e9e9e;
}

.card .card-action a:hover:not(.btn):not(.btn-large):not(.btn-large):not(.btn-floating) {
  color: #ff4081;
}

.bdbg-content__single .card-action {
  border-top: 1px solid rgba(160, 160, 160, 0.2);
  border-bottom: 1px solid rgba(160, 160, 160, 0.2);
}

.card .card-action .cat-links {
  display: inline-block;
  margin-right: 20px;
}

.card .card-action .cat-links a:not(.btn):not(.btn-large):not(.btn-large):not(.btn-floating) {
  margin-right: 0;
}

.bdbg-post__readmore {
  padding-top: 1rem;
}

.screen-reader-text {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}
